.Task {
  display: flex;
  /* align-items: center; */
  margin: 15px 0;
  width: 100%;
  font-size: 15px;
}

.TaskName:hover {
  cursor: pointer;
}

.checkbox {
  box-sizing: border-box;
  width: 43px;
  height: 35px;
  margin-right: 10px;
  border: 1px solid #1c437e;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.checkbox:hover {
  cursor: pointer;
}

.checked {
  border: 1px solid #007500;
  color: #007500;
}

.TaskDate {
  width: fit-content;
  padding: 6px 10px;
  background-color: #f0f5fb;
  border-radius: 5px;
  margin-top: 10px;
  font-weight: bold;
}

.modalChildren {
  display: flex;
  flex-flow: column;
  padding: 10px;
  width: 100%;
  font-size: 13px;
  box-sizing: border-box;
}

.modalChildren > * {
  width: 100%;
  margin: 10px 0;
}

.CloseIcon:hover {
  cursor: pointer;
}

.modalChildren input {
  background-color: #f0f5fb;
  font-family: "Open Sans", sans-serif;
  color: #1c437e;
  border: none;
  padding: 5px 15px;
  box-sizing: border-box;
  outline: none;
}

.modalChildren textarea {
  background-color: #f0f5fb;
  font-family: "Open Sans", sans-serif;
  color: #1c437e;
  border: none;
  padding: 5px 15px;
  box-sizing: border-box;
  height: 70px;
  resize: vertical;
  outline: none;
}

.modalChildren textarea::placeholder {
  color: #90c0e7;
}

.modalChildren > div:first-child {
  display: flex;
  align-items: center;
}

.modalChildren > div:first-child *:last-child {
  margin-left: auto;
}
